@use '../variables' as *;

.ScrollToTop {
    position: fixed;
    bottom: 20px;
    right: 20px;
    font-size: 42px;
    background: none;
    border: none;
    color: $RaisinBlack-03;
    cursor: pointer;
    transition: $Transition-03;
    &:hover {
        transform: translateY(-5px);
    }
}